<template>
	<div style="height: 100%;">
		<h1 class="table-title">{{tableTitle}}</h1>
		<el-table :data="tableData.filter(data => !searchProv || data.prov.includes(searchProv.toLowerCase()))" stripe
			style="width: 100%;" height="100%">
			<el-table-column prop="prov" label="省份" width="70" fixed>
			</el-table-column>
			<el-table-column label="年份">
				<el-table-column prop="y2011" label="2011" width="100" sortable>
				</el-table-column>
				<el-table-column prop="y2012" label="2012" width="100" sortable>
				</el-table-column>
				<el-table-column prop="y2013" label="2013" width="100" sortable>
				</el-table-column>
				<el-table-column prop="y2014" label="2014" width="100" sortable>
				</el-table-column>
				<el-table-column prop="y2015" label="2015" width="100" sortable>
				</el-table-column>
				<el-table-column prop="y2016" label="2016" width="100" sortable>
				</el-table-column>
				<el-table-column prop="y2017" label="2017" width="100" sortable>
				</el-table-column>
				<el-table-column prop="y2018" label="2018" width="100" sortable>
				</el-table-column>
				<el-table-column prop="y2019" label="2019" width="100" sortable>
				</el-table-column>
				<el-table-column prop="y2020" label="2020" width="100" sortable>
				</el-table-column>
			</el-table-column>
		</el-table>
		<div class="tool-box">
			<el-popover placement="right" width="200" trigger="click">
				<el-row>
					<el-col :span="10" class="label-text">
						数据类型：
					</el-col>
					<el-col :span="14">
						<el-select style="width: 120px;" v-model="dataType" placeholder="数据类型">
							<el-option label="人口数据" value="population"></el-option>
							<el-option label="经济数据" value="gdp"></el-option>
							<el-option label="工资数据" value="salary"></el-option>
							<el-option label="房价数据" value="house-price"></el-option>
						</el-select>
					</el-col>
				</el-row>
				<p></p>
				<el-row>
					<el-col :span="10" class="label-text">
						省份：
					</el-col>
					<el-col :span="14">
						<el-select v-model="searchProv" filterable placeholder="省份">
							<el-option label="全部" value=""></el-option>
							<el-option v-for="item,index in tableData" :key="index" :label="item.prov"
								:value="item.prov">
							</el-option>
						</el-select>
					</el-col>
				</el-row>
				<sub>数据来源于网络</sub>
				<i class="el-icon-s-tools" slot="reference"></i>
			</el-popover>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'Table',
		data() {
			return {
				dataType: 'population',
				tableTitle: '人口数据',
				tableData: [],
				searchProv: ''
			}
		},
		methods: {
			setDataInfo() {
				if (this.dataType == 'population') {
					this.tableTitle = '各省份人口数据(万人)'
				} else if (this.dataType == 'gdp') {
					this.tableTitle = '各省份GDP数据(亿元)'
				} else if (this.dataType == 'salary') {
					this.tableTitle = '各省份工资数据(元)'
				} else if (this.dataType == 'house-price') {
					this.tableTitle = '各省份房价数据(元/m²)'
				}
				this.$axios.get(process.env.BASE_URL + 'data/table/' + this.dataType + '.dat').then(resp => {
					this.tableData = eval(resp.data);
				})
			}
		},
		mounted() {
			this.setDataInfo()
		},
		watch: {
			dataType() {
				this.setDataInfo();
			}
		}
	}
</script>

<style>
	.table-title {
		text-align: center;
		line-height: 20px;
		margin: 2px auto 8px;
	}
</style>
